import React from 'react';
import { Col, Space, Typography } from 'antd';

const { Title, Text } = Typography

export default function Course ({ dataSource = [], courseId, setCourseId }) {
  return (
        <Col span={24}>
            <Space direction="vertical" style={{ width: '100%', }}>
                <Title level={4}>Course</Title>
                {
                    dataSource.map(item =>
                        <Text
                            key={item.id}
                            style={{
                              width: '100%',
                              padding: 2,
                              display: 'inline-block',
                              background: courseId === item.id ? '#96b2cb' : '#fff',
                              color: courseId === item.id ? '#fff' : 'rgba(0, 0, 0, 0.85)',
                              cursor: 'pointer',
                            }}
                            onClick={() => setCourseId(item.id)}
                        >
                            {item.courseName}
                        </Text>
                    )
                }
            </Space>
        </Col>
  )
}
