import React, { useCallback, useEffect, useState } from 'react';
import { Layout, Col, Row, Button, Divider, Input, } from 'antd';
import { useNavigate } from 'react-router-dom';
import Header from './header';
import { Course, Category, Question } from '../components';
import { currentUser, courses, categorys, questionCourse, questionCourseCategory } from '../services';

const { Search } = Input;
const { Content, Footer, Sider } = Layout;

export default function CustomLayout ({ children }) {
  const navigate = useNavigate();
  const [visible, setVisible] = useState(false);
  const [userInfo, setUserInfo] = useState({ email: '', username: '', id: '' })
  const [course, setCourse] = useState([]);
  const [category, setCategory] = useState([]);
  const [questions, setQuestions] = useState([]);

  const [courseId, setCourseId] = useState(0);
  const [categoryId, setCategoryId] = useState(0);
  const [questionId, setQuestionId] = useState(0);

  const getCurrentUser = useCallback(async () => {
    const res = await currentUser();
    setUserInfo(res)
  }, [])

  const getCourses = useCallback(async () => {
    const { data = [] } = await courses();
    setCourse(data)
  }, [])

  const getCategorys = useCallback(async () => {
    const { data = [] } = await categorys();
    setCategory(data)
  }, [])

  const getCourseQuestions = useCallback(async (params) => {
    const { data = [] } = await questionCourse(params);
    setQuestions(data)
  }, [])

  const getCourseCategoryQuestions = useCallback(async (params) => {
    const { data = [] } = await questionCourseCategory(params);
    setQuestions(data)
  }, [])

  const onQuestionClick = (id) => {
    navigate(`/answers?id=${id}`)
  }

  useEffect(() => {
    getCurrentUser();
  }, [getCurrentUser]);

  useEffect(() => {
    getCourses();
  }, [getCourses]);

  useEffect(() => {
    getCategorys();
  }, [getCategorys]);

  useEffect(() => {
    if (courseId) {
      if (categoryId) {
        getCourseCategoryQuestions({ courseId, categoryId });
      } else {
        getCourseQuestions({ courseId });
      }
    }
  }, [getCourseQuestions, courseId, categoryId]);

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Header
        visible={visible}
        setVisible={setVisible}
        getCourses={getCourses}
        getCurrentUser={getCurrentUser}
        userInfo={userInfo}
      />
      <Layout>
        <Sider
          width={600}
          breakpoint="lg"
          collapsedWidth="0"
          onBreakpoint={broken => {
            console.log(broken);
          }}
          onCollapse={(collapsed, type) => {
            console.log(collapsed, type);
          }}
          style={{ backgroundColor: '#fff', padding: 8 }}
        >
          <Row>
            <Col span={12}>
              <Row gutter={24}>
                <Col span={24}>
                  <Button type='primary' size='large' onClick={() => setVisible(!visible)}>
                    Add New Course
                  </Button>
                </Col>
                <Course dataSource={course} courseId={courseId} setCourseId={setCourseId} />
                <Category dataSource={category} categoryId={categoryId} setCategoryId={setCategoryId} />
              </Row>
            </Col>
            <Col span={1}><Divider type="vertical" style={{ minHeight: '100vh' }} /></Col>
            <Col span={11}>
              <Row>
                <Col span={24}>
                  <Search placeholder="input search text" style={{ width: '100%' }} />
                </Col>
                <Question
                  dataSource={questions}
                  questionId={questionId}
                  setQuestionId={setQuestionId}
                  onClick={onQuestionClick}
                />
              </Row>

            </Col>
          </Row>
        </Sider>
        <Content style={{ margin: '24px 16px 0', background: '#fff' }}>
          <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
            {children}
          </div>
        </Content>
      </Layout>
      <Footer style={{ textAlign: 'center' }}>Online Discussion Forum</Footer>
    </Layout>
  );
}
