import React from 'react';
import { Modal, Form, Input } from 'antd';

export default function UserNameModal ({ visible, setVisible, callback, service }) {
  const [form] = Form.useForm();
  const handleOk = async () => {
    form.validateFields().then(async value => {
      const { message } = await service(value);
      if (message === 'success') {
        callback();
        setVisible(!visible)
      }
    })
  }
  return (
        <Modal
            key='Edit UserName'
            title='Edit UserName'
            open={visible}
            onCancel={() => setVisible(!visible)}
            onOk={handleOk}
        >
            <Form
                form={form}
                autoComplete="off"
            >
                <Form.Item
                    label="UserName"
                    name="userName"
                    rules={[{ required: true, message: 'Please input your userName!' }]}>
                    <Input />
                </Form.Item>
            </Form>
        </Modal>
  )
}
