import React, { useState } from 'react';
import { Layout, Menu, Col, Row, Avatar, Space, Dropdown, Form } from 'antd';
import { DownloadOutlined, CommentOutlined, HomeOutlined, AlertOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import { courseInsert, currentUserUpdate, logout } from '../services';
import { CourseModal, UserNameModal } from '../containers';

const { Header } = Layout;

const css = {
  mainColor: '#51288c'
}

const iconStyle = { fontSize: 16 }

const navItems = [
  { key: 'download', icon: <DownloadOutlined style={iconStyle} />, label: 'Download' },
  { key: 'comment', icon: <CommentOutlined style={iconStyle} />, label: 'Comment' },
  { key: 'home', icon: <HomeOutlined style={iconStyle} />, label: 'Home' },
  { key: 'message', icon: <AlertOutlined style={iconStyle} />, label: 'Message' },
]

export default function CustomHeader ({ getCourses, getCurrentUser, userInfo, visible, setVisible }) {
  const navigate = useNavigate();
  const [form] = Form.useForm();
  const [userNameVisible, setUserNameVisible] = useState(false);

  const handleUserItemClick = async ({ key }) => {
    if (key === 'logout') {
      await logout();
      navigate('/login')
    }
    if (key === 'update') {
      setUserNameVisible(!userNameVisible);
    }
    if (key === 'login') {
      navigate(`/${key}`)
    }
  }

  const overlay = (
    <Menu
      onClick={handleUserItemClick}
      items={userInfo.id
        ? [
            {
              key: 'update',
              label: 'Edit UserName',
            },
            {
              key: 'logout',
              label: 'logout',
            },
          ]
        : [
            {
              key: 'login',
              label: 'login',
            },
          ]}
    />
  );

  return (
        <Header style={{ padding: 0, background: 'primary', backgroundColor: css.mainColor, overflow: 'hidden', height: 64 }}>
            <CourseModal
                key='Add New Course'
                title='Add New Course'
                visible={visible}
                setVisible={setVisible}
                service={courseInsert}
                callback={getCourses}
            />
            <UserNameModal
                form={form}
                key='Edit UserName'
                title='Edit UserName'
                visible={userNameVisible}
                setVisible={setUserNameVisible}
                service={currentUserUpdate}
                callback={getCurrentUser}
            />
            <Row align='middle' justify='space-between' style={{ height: 64 }}>
                <Col span={2} className="logo" style={{ height: 64, paddingLeft: 8, color: '#fff', fontWeight: 700 }}>
                    Online Discussion Forum
                </Col>
                <Col span={6} offset={14}>
                    <Menu
                        style={{ backgroundColor: css.mainColor, color: '#fff', padding: 0 }}
                        mode="horizontal"
                        items={navItems}
                    />
                </Col>
                <Col span={2} className="logo" style={{ paddingLeft: 8, color: '#fff', fontWeight: 700 }}>
                    <Dropdown overlay={overlay}>
                        <a onClick={e => e.preventDefault()}>
                            <Space>
                                <Avatar style={{ verticalAlign: 'middle', cursor: 'pointer', color: css.mainColor, backgroundColor: '#fff' }} size="small" gap={1}>
                                    {userInfo.username}
                                </Avatar>
                            </Space>
                        </a>
                    </Dropdown>
                </Col>
            </Row>
        </Header>
  )
}
