import React, { createElement } from 'react';
import { Space, List } from 'antd';
import { LikeOutlined, PushpinOutlined, CommentOutlined } from '@ant-design/icons';

const IconText = ({ icon, text, ...props }) => (
    <Space>
        {createElement(icon, props)}
        {text}
    </Space>
);

export default function Answer ({ dataSource = [], onPin, onLike, onComment }) {
  return (
        <List
            size='large'
            itemLayout="horizontal"
            dataSource={dataSource}
            renderItem={item => (
                <List.Item
                    key={item.id}
                    actions={[
                        <IconText icon={PushpinOutlined} style={{ cursor: 'pointer', color: item.pin ? '#51288c' : 'rgba(0, 0, 0, 0.45)', }} key="pin" onClick={() => onPin(item.id, item.pin)} />,
                        <IconText icon={LikeOutlined} style={{ cursor: 'pointer', color: item.like ? '#51288c' : 'rgba(0, 0, 0, 0.45)' }} key="like" onClick={() => onLike(item.id, item.lick)}/>,
                        <IconText icon={CommentOutlined} style={{ cursor: 'pointer' }} text={item.reviews.length} key="comment" onClick={() => onComment(item.id)}/>,
                    ]}
                >
                    <List.Item.Meta
                        title={item.content}
                    />
                    {item.updatedAt.split('T')[0]}
                </List.Item>
            )}
        />
  );
}
